.card {
	width: 79px;
	height: 123px;
	margin: 10px;
	position: absolute;
}

.cards {
	overflow-x: scroll;
	width: 100%;
	height: 143px;
	position: relative;
}

.deck, .down-stack {
	margin: 0px auto;
	width: 152px;
}

.pile {
	width: calc(100% - 152px);
	min-width: 348px;
}


