.App {
    text-align: center;
    height: 100%;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-intro {
    font-size: large;
}

.App-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    min-height: calc(100% - 190px);
}

.side-spacer {
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    min-width: 20px;
}

.spacer-left {
    background: darksalmon;
    background: -webkit-gradient(linear, left top, right top, from(indianred), to(darksalmon));
    background: -webkit-linear-gradient(left, indianred, darksalmon);
    background: -o-linear-gradient(left, indianred, darksalmon);
    background: linear-gradient(to right, indianred, darksalmon);
}

.spacer-right {
    background: darksalmon;
    background: -webkit-gradient(linear, right top, left top, from(indianred), to(darksalmon));
    background: -webkit-linear-gradient(right, indianred, darksalmon);
    background: -o-linear-gradient(right, indianred, darksalmon);
    background: linear-gradient(to left, indianred, darksalmon);
}

@-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.war {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    background: darksalmon;
}

.war-panel {
    width: 200px;
    margin-right: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column; 
}

.players {
    width: 500px;
}



